import request from '@/utils/request'

	export function uinfo(data) {
	    return request({
	      	url: '/user/info',
	      	method: 'get',
	      	params:data
	    })
	}

	// todo
	export function umodifyInfo(data) {
	    return request({
	      	url: '/user/modifyInfo',
	      	method: 'get',
	      	params:data
	    })
	}